"use client";

import { useMemo, useState, useEffect } from "react";
// 1. OBTENCIÓN DE DATOS CENTRALIZADA
import { useProductosPromo } from "@/hooks/useProductsPromo";
import { ProductoConPromo, Reseña } from "@/lib/types"; // Usamos los tipos unificados

import ProductCard from "./components/ProductCard";
import SearchBar from "./components/SearchBar";
import FilterButton from "./components/FilterButton";
import FilterModal from "./components/FilterModal";
import ProductDetailModal from "./components/ProductDetailModal";
import { useCart } from "@/lib/cart-context";
import { LoaderCircle, AlertTriangle } from "lucide-react";
import { obtenerReseñas } from "./data/reviews";

// --- Helpers ---
const getDefaultOpts = (
    p: ProductoConPromo
): Record<string, string | number> | undefined => {
    if (!p.options) return undefined;
    const def: Record<string, string | number> = {};
    for (const [k, vals] of Object.entries(p.options)) {
        if (Array.isArray(vals) && vals.length) def[k] = vals[0];
    }
    return Object.keys(def).length ? def : undefined;
};

export default function CatalogoPage() {
    // OBTENCIÓN DE DATOS CENTRALIZADA
    // Ahora obtenemos los 3 estados del hook para un manejo robusto.
    const {
        productos,
        loading: loadingProducts,
        error: errorProducts,
    } = useProductosPromo();
    const [allReviews, setAllReviews] = useState<Reseña[]>([]);
    const [loadingReviews, setLoadingReviews] = useState(true);

    useEffect(() => {
        // Obtenemos las reseñas desde el nuevo servicio API
        obtenerReseñas()
            .then(setAllReviews)
            .catch(console.error) // Manejo básico de errores de reseñas
            .finally(() => setLoadingReviews(false));
    }, []);

    // --- Estados de filtros UI ---
    const [min, setMin] = useState(0);
    const [max, setMax] = useState(0);
    const [search, setSearch] = useState("");
    const [tag, setTag] = useState<string | null>(null);
    const [promos, setPromos] = useState(false);
    const [nuevos, setNuevos] = useState(false);
    const [rating45, setRating45] = useState(false);
    const [showFilters, setShowFilters] = useState(false);
    const [details, setDetails] = useState<ProductoConPromo | null>(null);

    // --- Efecto para el rango de precios ---
    useEffect(() => {
        if (productos.length) {
            const precios = productos.map((p) => p.price);
            const minPrice = Math.min(...precios);
            const maxPrice = Math.max(...precios);
            setMin(minPrice);
            setMax(maxPrice);
        }
    }, [productos]);

    // --- Lógica del carrito ---
    const { addItem } = useCart();

    const addSimple = (p: ProductoConPromo) =>
        addItem({
            id: String(p.id),
            name: p.name,
            price: p.price,
            image: p.image,
            quantity: 1,
            options: getDefaultOpts(p),
            promo: p.promo,
        });

    const addWithOpts = (
        p: ProductoConPromo,
        opts: Record<string, string | number>,
        qty: number
    ) =>
        addItem({
            id: String(p.id),
            name: p.name,
            price: p.price,
            image: p.image,
            quantity: qty,
            options: opts,
            promo: p.promo,
        });

    // 3. OPTIMIZACIÓN: Pre-cálculo de Ratings
    // Calculamos el rating promedio de cada producto UNA SOLA VEZ y lo guardamos en un Map.
    const averageRatings = useMemo(() => {
        const ratingsMap = new Map<number, number>();
        if (allReviews.length === 0) return ratingsMap;

        productos.forEach((p) => {
            const reviews = allReviews.filter((r) => r.productoId === p.id);
            const avg = reviews.length
                ? reviews.reduce((s, r) => s + r.calificacion, 0) /
                  reviews.length
                : 0;
            ratingsMap.set(p.id, avg);
        });
        return ratingsMap;
    }, [productos, allReviews]);

    // 4. LÓGICA DE FILTRADO ACTUALIZADA
    // Ahora es mucho más rápido porque lee el rating pre-calculado del Map.
    const list = useMemo(() => {
        const term = search.trim().toLowerCase();
        return productos.filter((p) => {
            const okName = !term || p.name.toLowerCase().includes(term);
            const okTag = !tag || p.tags.includes(tag);
            const okPrice = p.price >= min && p.price <= max;
            const okPromos = !promos || !!p.promo;
            const okNuevos = !nuevos || p.promo?.tipo === "nuevo";
            const okRating =
                !rating45 || (averageRatings.get(p.id) ?? 0) >= 4.5;
            return (
                okName && okTag && okPrice && okPromos && okNuevos && okRating
            );
        });
    }, [
        productos,
        search,
        tag,
        min,
        max,
        promos,
        nuevos,
        rating45,
        averageRatings,
    ]);

    // --- Etiquetas y reset ---
    const allTags = useMemo(
        () => Array.from(new Set(productos.flatMap((p) => p.tags))),
        [productos]
    );

    const reset = () => {
        setSearch("");
        setTag(null);
        setPromos(false);
        setNuevos(false);
        setRating45(false);
        if (productos.length) {
            const precios = productos.map((p) => p.price);
            setMin(Math.min(...precios));
            setMax(Math.max(...precios));
        }
    };

    // 2. MANEJO DE ESTADOS DE CARGA Y ERROR
    if (loadingProducts || loadingReviews) {
        return (
            <main className="min-h-screen flex items-center justify-center">
                <LoaderCircle className="w-12 h-12 animate-spin text-green-500" />
            </main>
        );
    }

    if (errorProducts) {
        return (
            <main className="min-h-screen flex flex-col items-center justify-center text-red-500">
                <AlertTriangle className="w-16 h-16 mb-4" />
                <h2 className="text-2xl font-bold">
                    ¡Ay, caramba! No se pudieron cargar los productos.
                </h2>
                <p>{errorProducts.message}</p>
            </main>
        );
    }

    // --- UI ---
    return (
        <main className="min-h-screen bg-yellow-50 dark:bg-gray-800 py-10 px-6">
            <h1 className="text-4xl font-extrabold text-green-500 dark:text-yellow-500 text-center mb-4">
                🛒 Productos del Kwik-E-Mart
            </h1>
            <p className="text-center text-lg text-gray-700 dark:text-white mb-6">
                Explora nuestra variedad de productos y añade tus favoritos al
                carrito.
            </p>

            {/* buscador + filtros */}
            <SearchBar value={search} onChange={setSearch} />
            <FilterButton onClick={() => setShowFilters(true)} />

            {list.length === 0 ? (
                <p className="text-center text-gray-600 mt-8">
                    No encontramos productos que coincidan con tu búsqueda.
                </p>
            ) : (
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 max-w-6xl mx-auto mt-8">
                    {list.map((p) => (
                        <ProductCard
                            key={p.id}
                            producto={p}
                            onAdd={addSimple}
                            onSelect={setDetails}
                        />
                    ))}
                </div>
            )}

            {/* modal filtros */}
            <FilterModal
                show={showFilters}
                onClose={() => setShowFilters(false)}
                etiquetas={allTags}
                etiquetaActiva={tag}
                setEtiqueta={setTag}
                precioMin={min}
                setPrecioMin={setMin}
                precioMax={max}
                setPrecioMax={setMax}
                limpiar={reset}
                promos={promos}
                setPromos={setPromos}
                rating45={rating45}
                setRating45={setRating45}
                nuevos={nuevos}
                setNuevos={setNuevos}
            />

            {/* modal detalle */}
            <ProductDetailModal
                show={Boolean(details)}
                product={details}
                onClose={() => setDetails(null)}
                onAdd={addWithOpts}
            />
        </main>
    );
}
