// lib/services/promoService.ts

import { Promo, PromoProductoRelacion } from "../types";

// Para crear la relación no necesitamos el campo `id`
export type PromoProductCreate = {
  idPromo: number;
  idProducto: number;
};

const authHeader = (): HeadersInit => {
    const token = localStorage.getItem("accessToken");
    return token ? { Authorization: `Bearer ${token}` } : {};
};

const API_BASE_URL = "/api/8080/catalog-service";

/**
 * GET /promotions
 */
export async function getPromos(): Promise<{ promotions: Promo[] }> {
  const res = await fetch(`${API_BASE_URL}/promotions/`, {
    headers: authHeader(),
  });
  if (!res.ok) throw new Error("No se pudieron obtener las promociones");
  return res.json();
}

/**
 * POST /promotions
 */
export async function createPromo(
  promoData: Omit<Promo, "id">
): Promise<Promo> {
  const res = await fetch(`${API_BASE_URL}/promotions/`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      ...authHeader(),
    },
    body: JSON.stringify(promoData),
  });
  if (!res.ok) throw new Error("Error al crear la promoción");
  return res.json();
}

/**
 * GET /promotions/promo-productos
 */
export async function getProductPromoRelations(): Promise<{
  products: PromoProductoRelacion[];
}> {
  const res = await fetch(`${API_BASE_URL}/promotions/promo-productos/`, {
    headers: authHeader(),
  });
  if (!res.ok)
    throw new Error("No se pudieron obtener relaciones promoción–producto");
  return res.json();
}

/**
 * POST /promotions/promo-productos
 */
export async function assignPromoToProduct(
  relation: PromoProductCreate
): Promise<void> {
  const res = await fetch(`${API_BASE_URL}/promotions/promo-productos/`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      ...authHeader(),
    },
    body: JSON.stringify(relation),
  });
  if (!res.ok)
    throw new Error("Error al asignar la promoción al producto");
}

/**
 * Helper cliente‑side para filtrar promociones de un producto
 */
export async function getPromosForProduct(
  productId: number
): Promise<Promo[]> {
  const [all, { products: rels }] = await Promise.all([
    getPromos(),
    getProductPromoRelations(),
  ]);
  const ids = rels
    .filter((r) => r.idProducto === productId)
    .map((r) => r.idPromo);
  return all.promotions.filter((p) => ids.includes(p.id));
}
