// components/catalogo/SearchBar.tsx ───────────────────────────────────────
"use client";

/**
 * Props de `SearchBar`.
 * @property value    Texto actual del input.
 * @property onChange Callback para actualizar el texto.
 */
interface Props {
  value: string;
  onChange: (v: string) => void;
}

/**
 * **SearchBar**
 *
 * Barra de búsqueda simple.
 */
export default function SearchBar({ value, onChange }: Props) {
  return (
    <div className="flex justify-center mb-6">
      <input
        type="text"
        placeholder="🔍 Buscar producto…"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        className="w-full max-w-md px-5 py-3 rounded-xl bg-white shadow-sm
                   border border-green-300 text-black focus:outline-none
                   focus:ring-2 focus:ring-green-400"
      />
    </div>
  );
}
