// components/catalogo/ProductDetailModal.tsx
"use client";

import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { Button } from "@/components/ui/button";
import Image from "next/image";
import type { ProductoConPromo, Reseña } from "@/lib/types";
import { getTotalPromo } from "@/lib/precio-producto";
import { reseñas as initialReviews } from "../data/reviews";

import useBodyScrollLock from "@/hooks/useBodyScrollLock";
import useEscapeKey from "@/hooks/useEscapeKey";

import QuantitySelector from "./QuantitySelector";
import OptionSelect from "./OptionSelect";
import ReviewForm from "./ReviewForm";
import ReviewList from "./ReviewList";

interface Props {
    product: ProductoConPromo | null;
    show: boolean;
    onClose: () => void;
    /**
     * Añade al carrito:
     * - `p`      → producto
     * - `opts`   → opciones seleccionadas
     * - `qty`    → cantidad
     */
    onAdd: (
        p: ProductoConPromo,
        opts: Record<string, string | number>,
        qty: number
    ) => void;
}

type ReviewInput = { nombre: string; comentario: string; calificacion: number };

/**
 * **ProductDetailModal**
 *
 * Modal con detalles, opciones, cantidad, precio dinámico y reseñas.
 * Maneja bloqueo de scroll y cierre con `Esc`.
 */
export default function ProductDetailModal({
    product,
    show,
    onClose,
    onAdd,
}: Props) {
    /* -------------------- state & derived -------------------- */
    const [sel, setSel] = useState<Record<string, string>>({});
    const [cantidad, setCantidad] = useState(1);

    const [reviews, setReviews] = useState<Reseña[]>(initialReviews);
    const [openReviews, setOpenReviews] = useState(false);
    const reviewsEndRef = useRef<HTMLDivElement>(null);

    /* ---------- reset opciones al cambiar de producto ---------- */

    useEffect(() => {
        const defaultOptions: Record<string, string> = {};
        if (product?.options) {
            Object.entries(product.options).forEach(([key, valueArray]) => {
                // Solo establece un valor por defecto si el array de opciones es válido
                if (Array.isArray(valueArray) && valueArray.length > 0) {
                    defaultOptions[key] = valueArray[0];
                }
            });
        }
        setSel(defaultOptions);
        setCantidad(1); // Resetea la cantidad al cambiar de producto
    }, [product]);

    /* ---------- hooks de UX ---------- */
    useBodyScrollLock(show);
    useEscapeKey(onClose, show);

    /* ---------- callbacks ---------- */

    const handleAddToCart = useCallback(() => {
        onAdd(product!, sel, cantidad);
        onClose();
    }, [product, sel, cantidad, onAdd, onClose]);

    const handleSubmitReview = useCallback(
        ({ nombre, comentario, calificacion }: ReviewInput) => {
            if (!product) return;
            setReviews((prev) => [
                ...prev,
                {
                    id: Date.now(),
                    productoId: product.id,
                    usuario: nombre,
                    comentario,
                    calificacion,
                    fecha: new Date().toISOString().split("T")[0],
                },
            ]);
            setTimeout(
                () =>
                    reviewsEndRef.current?.scrollIntoView({
                        behavior: "smooth",
                    }),
                150
            );
        },
        [product]
    );

    const filteredReviews = useMemo(
        () =>
            product ? reviews.filter((r) => r.productoId === product.id) : [],
        [product, reviews]
    );

    /* ---------- precio total ---------- */
    const { total, old, tag, value } = useMemo(
        () =>
            product
                ? getTotalPromo(product.promo, product.price, cantidad)
                : { total: 0, old: null, tag: null, value: null },
        [product, cantidad]
    );

    /* ---------- early exit ---------- */
    if (!show || !product) return null;

    /* ---------- badge helpers ---------- */
    const badgeBase = "px-3 py-1 rounded-full font-simpson text-base shadow";
    const badgePromo =
        "bg-blue-500 text-yellow-300 dark:bg-yellow-300 dark:text-blue-600 border-2 border-blue-700 dark:border-yellow-400";
    const badgeTipo =
        "bg-yellow-300 text-blue-600 dark:bg-blue-700 dark:text-yellow-300 border-2 border-yellow-500 dark:border-blue-400";

    /* -------------------- UI -------------------- */
    return (
        <div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm p-4"
            style={{ overflowY: "auto" }}>
            {/* overlay (para cerrar) */}
            <button
                aria-label="Cerrar"
                onClick={onClose}
                className="absolute inset-0 cursor-default"
                tabIndex={-1}
            />

            {/* modal */}
            <div className="relative w-full max-w-2xl bg-white dark:bg-slate-800 rounded-2xl shadow-xl max-h-[90vh] flex flex-col overflow-y-auto">
                {/* header */}
                <header className="flex justify-between items-center p-4 border-b dark:border-white/30">
                    <div className="flex flex-col gap-1 flex-1">
                        <div className="flex items-center flex-wrap gap-2">
                            <h2 className="text-xl font-bold dark:text-gray-100">
                                {product.name}
                            </h2>
                            {product.promo && (
                                <>
                                    <span
                                        className={`${badgeBase} ${badgePromo}`}>
                                        PROMO
                                    </span>
                                    {tag && (
                                        <span
                                            className={`${badgeBase} ${badgeTipo}`}>
                                            {tag === "Descuento" ? value : tag}
                                        </span>
                                    )}
                                </>
                            )}
                        </div>
                    </div>
                    <button
                        onClick={onClose}
                        className="p-2 rounded-full  hover:bg-gray-100 hover:dark:text-black cursor-pointer transition-colors">
                        ✕
                    </button>
                </header>

                {/* body */}
                <section className="px-5 py-6 flex flex-col gap-4 flex-1">
                    {/* info producto */}
                    <div className="flex flex-col md:flex-row md:gap-8 items-center">
                        <Image
                            src={product.image}
                            alt={product.name}
                            width={224}
                            height={224}
                            className="w-40 h-40 md:w-56 md:h-56 object-contain mb-3 md:mb-0"
                        />

                        <div className="flex-1 flex flex-col gap-4">
                            <p className="text-gray-800 dark:text-gray-200">
                                {product.description}
                            </p>

                            {/* opciones */}
                            {Array.isArray(product.options) &&
                                Object.entries(product.options).map(
                                    ([k, vals]) => (
                                        <OptionSelect
                                            key={k}
                                            label={k}
                                            value={sel[k]}
                                            options={vals}
                                            onChange={(v) =>
                                                setSel((s) => ({
                                                    ...s,
                                                    [k]: v,
                                                }))
                                            }
                                        />
                                    )
                                )}

                            {/* cantidad + precio */}
                            <div className="flex items-center gap-4 mt-3">
                                <QuantitySelector
                                    value={cantidad}
                                    onChange={setCantidad}
                                />

                                <span className="ml-2 flex items-end gap-2">
                                    {old && (
                                        <span className="text-red-500 line-through text-lg select-none">
                                            ${old.toFixed(2)}
                                        </span>
                                    )}
                                    <span className="text-green-600 dark:text-green-400 select-none text-2xl font-extrabold">
                                        ${total.toFixed(2)}
                                    </span>
                                    {cantidad > 1 && (
                                        <span className="text-xs text-gray-500">
                                            (total)
                                        </span>
                                    )}
                                </span>
                            </div>

                            {/* botones */}
                            <div className="flex items-center gap-4 mt-3">
                                <Button
                                    onClick={onClose}
                                    className="flex-1 py-2 rounded-xl border text-black dark:text-white dark:hover:bg-gray-600 hover:text-white border-gray-300 dark:border-white/20 bg-white dark:bg-white/50 cursor-pointer">
                                    Cancelar
                                </Button>
                                <button
                                    onClick={handleAddToCart}
                                    className="flex-1 py-2 rounded-xl bg-green-500 hover:bg-green-600 text-white cursor-pointer transition">
                                    Añadir al carrito
                                </button>
                            </div>
                        </div>
                    </div>

                    {/* reseñas */}
                    <ReviewForm onSubmit={handleSubmitReview} />

                    <div className="flex items-center justify-between w-full mb-2">
                        <h3 className="text-lg font-bold text-gray-800 dark:text-white">
                            Reseñas
                        </h3>
                        <button
                            type="button"
                            className="text-blue-600 hover:underline font-bold text-sm dark:text-blue-400 cursor-pointer"
                            onClick={() => setOpenReviews((v) => !v)}>
                            {openReviews ? "Ocultar" : "Mostrar"}
                        </button>
                    </div>

                    {openReviews && (
                        <>
                            <ReviewList reviews={filteredReviews} />
                            <div ref={reviewsEndRef} />
                        </>
                    )}
                </section>
            </div>
        </div>
    );
}
