// components/catalogo/QuantitySelector.tsx
"use client";

type Props = {
    value: number;
    onChange: (n: number) => void;
    min?: number;
    max?: number; // (no usado aún, por si se quiere limitar)
};

/**
 * **QuantitySelector**
 *
 * Selector de cantidad con – / + y un icono de “basura” cuando está en el
 * mínimo (semántica “eliminar”).
 */
export default function QuantitySelector({ value, onChange, min = 1 }: Props) {
    return (
        <div className="flex items-center border rounded-xl px-2 py-1 min-w-[100px] bg-white dark:bg-white/10 border-gray-200 dark:border-white/20">
            {/* Disminuir / eliminar */}
            <button
                aria-label={value > min ? "Disminuir" : "Eliminar"}
                className="p-1"
                onClick={() => onChange(Math.max(min, value - 1))}>
                {value === min ? (
                    /* trash icon */
                    <svg
                        className="w-5 h-5 text-gray-500 cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-100/10 rounded-xl"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth={2}
                        viewBox="0 0 24 24">
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M6 7h12M9 7V6a2 2 0 012-2h2a2 2 0 012 2v1m-7 0v10a2 2 0 002 2h4a2 2 0 002-2V7M10 11v6m4-6v6"
                        />
                    </svg>
                ) : (
                    /* minus icon */
                    <svg
                        className="w-5 h-5 text-gray-500 cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-100/10 rounded-xl"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth={2}
                        viewBox="0 0 24 24">
                        <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            d="M20 12H4"
                        />
                    </svg>
                )}
            </button>

            <span className="w-6 text-center font-mono text-lg select-none text-gray-800 dark:text-white">
                {value}
            </span>

            {/* Aumentar */}
            <button
                aria-label="Aumentar"
                className="p-1 cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-100/10 rounded-xl"
                onClick={() => onChange(value + 1)}>
                <svg
                    className="w-5 h-5 text-gray-500"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth={2}
                    viewBox="0 0 24 24">
                    <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M12 4v16m8-8H4"
                    />
                </svg>
            </button>
        </div>
    );
}
