import { Producto } from "@/lib/types";
import { refresh } from "./userService"; // si quieres auto‑refresh

const API_BASE_URL = "/api/8080/catalog-service";

/* ------------------------------------------------------------------ */
/* Helper para añadir Authorization                                    */
/* ------------------------------------------------------------------ */
const authHeader = (): HeadersInit => {
    const token = localStorage.getItem("accessToken");
    console.log("Auth header:", token)
    return token ? { Authorization: `Bearer ${token}` } : {};
};

/* ------------------------------------------------------------------ */
/* 1.  Rutas PÚBLICAS                                                  */
/* ------------------------------------------------------------------ */

export async function getProducts(): Promise<{ products: Producto[] }> {
    const res = await fetch(`${API_BASE_URL}/products/`);
    if (!res.ok)
        throw new Error("No se pudieron obtener los productos del Kwik‑E‑Mart");
    return res.json();
}

export async function getProductById(id: number): Promise<Producto> {
    const res = await fetch(`${API_BASE_URL}/products/${id}`);
    if (!res.ok)
        throw new Error(`No se pudo encontrar el producto con ID ${id}`);
    return res.json();
}

/* ------------------------------------------------------------------ */
/* 2.  Rutas PROTEGIDAS (requieren JWT)                               */
/* ------------------------------------------------------------------ */

export async function createProduct(
    product: Omit<Producto, "id">
): Promise<Producto> {
    const res = await fetch(`${API_BASE_URL}/products/`, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
            ...authHeader(),
        },
        body: JSON.stringify(product),
    });

    if (res.status === 401) {
        throw new Error("No autorizado: inicia sesión de nuevo.");
    }

    if (!res.ok) throw new Error("Error al intentar crear el producto");
    return res.json();
}

export async function updateProduct(
    id: number,
    product: Partial<Producto>
): Promise<Producto> {
    const res = await fetch(`${API_BASE_URL}/products/${id}`, {
        method: "PUT",
        headers: {
            "Content-Type": "application/json",
            ...authHeader(),
        },
        body: JSON.stringify(product),
    });

    if (res.status === 401)
        throw new Error("No autorizado: inicia sesión de nuevo.");
    if (!res.ok) throw new Error(`Error al actualizar el producto ${id}`);
    return res.json();
}

export async function deleteProduct(id: number): Promise<void> {
    const res = await fetch(`${API_BASE_URL}/products/${id}`, {
        method: "DELETE",
        headers: authHeader(),
    });

    if (res.status === 401)
        throw new Error("No autorizado: inicia sesión de nuevo.");
    if (!res.ok) throw new Error(`Error al eliminar el producto ${id}`);
}
