// components/catalogo/ReviewForm.tsx
"use client";

import { useEffect, useRef, useState, type FormEvent } from "react";
import StarRating from "./StarRating";

type Props = {
  /** Callback que recibe los datos validados y controlará el guardado */
  onSubmit: (d: { nombre: string; comentario: string; calificacion: number }) => void;
};

/**
 * **ReviewForm**
 *
 * Formulario compacto que se expande al hacer foco.
 * Valida campos requeridos y reinicia estado al enviar.
 */
export default function ReviewForm({ onSubmit }: Props) {
  const [open, setOpen]           = useState(false);
  const [nombre, setNombre]       = useState("");
  const [comentario, setComentario] = useState("");
  const [calificacion, setCalificacion] = useState(5);

  const inputRef = useRef<HTMLTextAreaElement>(null);

  /* Autofocus cuando se despliega */
  useEffect(() => { if (open) inputRef.current?.focus(); }, [open]);

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    if (!nombre.trim() || !comentario.trim()) return;

    onSubmit({ nombre, comentario, calificacion });
    setNombre(""); setComentario(""); setCalificacion(5); setOpen(false);
  };

  /* ------------- UI ------------- */
  return (
    <div
      className={`transition-all duration-300 bg-transparent border-b dark:border-white/30 pb-2 ${open ? "mb-2" : ""}`}
    >
      {!open ? (
        <button
          aria-label="Añade una reseña"
          className="flex w-full gap-2 text-gray-500 hover:text-gray-700 dark:text-white/70 dark:hover:text-white px-2 py-2 rounded"
          onClick={() => setOpen(true)}
        >
          <span className="flex-1 opacity-80 text-left">Añade una reseña…</span>
        </button>
      ) : (
        <form onSubmit={handleSubmit} className="flex flex-col gap-2">
          {/* Comentario */}
          <textarea
            ref={inputRef}
            required
            placeholder="Añade una reseña…"
            value={comentario}
            maxLength={200}
            onChange={(e) => setComentario(e.target.value)}
            rows={2}
            className="flex-1 resize-none rounded-lg border px-3 py-2 focus:ring-2 focus:ring-green-300 text-base text-gray-800 dark:text-white bg-white dark:bg-white/10 border-gray-300 dark:border-white/20"
            style={{ minHeight: 38 }}
            onBlur={(e) => {
              if (!e.target.value.trim() && !nombre.trim()) setOpen(false);
            }}
          />

          {(comentario.trim() || nombre.trim()) && (
            <div className="flex flex-col sm:flex-row gap-2 items-center justify-between">
              {/* Nombre */}
              <input
                type="text"
                required
                placeholder="Tu nombre"
                value={nombre}
                maxLength={30}
                onChange={(e) => setNombre(e.target.value)}
                className="rounded-lg border px-3 py-2 focus:ring-2 focus:ring-green-300 w-full sm:w-48 text-gray-800 dark:text-white bg-white dark:bg-white/10 border-gray-300 dark:border-white/20"
              />

              {/* Rating */}
              <div className="flex items-center gap-2">
                <span className="font-bold text-gray-700 dark:text-white">Calificación:</span>
                <StarRating value={calificacion} onChange={setCalificacion} />
              </div>

              {/* Publicar */}
              <button
                type="submit"
                className="ml-auto bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-6 rounded-xl transition"
              >
                Publicar
              </button>
            </div>
          )}
        </form>
      )}
    </div>
  );
}
