// lib/promo-adapter.ts (Versión corregida y enriquecida)

import { Promo, PromoAplicada } from "./types";

export function adaptApiPromo(promo?: Promo): PromoAplicada | undefined {
    if (!promo) {
        return undefined;
    }

    const basePromo = {
        id: promo.id,
        nombre: promo.nombre,
        descripcion: promo.descripcion,
        diasSemana: promo.diasSemana,
    };

    switch (promo.tipo) {
        case "descuento":
            if (typeof promo.valor === "number") {
                return { ...basePromo, tipo: "descuento", valor: promo.valor };
            }
            return undefined;
        case "2x1":
            return { ...basePromo, tipo: "2x1" };
        case "nuevo":
            return { ...basePromo, tipo: "nuevo" };
        case "solo-hoy":
            return { ...basePromo, tipo: "solo-hoy" };
        default:
            return undefined;
    }
}
