"use client";

/**
 * **FilterButton**
 *
 * Botón de acceso rápido al modal de filtros en la página de catálogo.
 */
export default function FilterButton({ onClick }: { onClick: () => void }) {
  return (
    <div className="flex items-center gap-2 justify-center mb-8">
      <span className="font-semibold dark:text-white">Filtrar por:</span>

      <button
        onClick={onClick}
        className="
          flex items-center gap-1 px-3 py-1.5 rounded-full border shadow-sm
          border-gray-300  bg-white  text-gray-800
          hover:bg-gray-100

          transition
        "
      >
        {/* Icono embudo */}
        <svg
          className="w-4 h-4"
          fill="none"
          stroke="currentColor"
          strokeWidth={2}
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="M3 4h18l-7 9v7H10v-7L3 4z"
          />
        </svg>
        <span className="text-sm hidden sm:inline">Abrir filtros</span>
      </button>
    </div>
  );
}
