// components/ui/StarRating.tsx ─────────────────────────────────────────────
"use client";

type Props = {
  /** Valor actual (1-5). */
  value: number;
  /** Manejador opcional cuando el usuario pulsa una estrella. */
  onChange?: (v: number) => void;
  /** Modo solo lectura (deshabilita interactividad y tab). */
  readOnly?: boolean;
};

/**
 * **StarRating**
 *
 * Renderiza 5 estrellas llenas/vacías y permite seleccionar la calificación
 * Sin estado interno: controlado por el padre.
 */
export default function StarRating({ value, onChange, readOnly }: Props) {
  return (
    <div className="flex gap-0.5">
      {[1, 2, 3, 4, 5].map((star) => (
        <button
          key={star}
          type="button"
          onClick={() => onChange?.(star)}
          disabled={readOnly}
          tabIndex={readOnly ? -1 : 0}
        >
          <svg
            className={`w-5 h-5 ${star <= value ? "text-yellow-400" : "text-gray-300"}`}
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path d="M10 15l-5.09 2.67 1-5.8-4.21-4.1 5.82-.85L10 2.5l2.48 5.02 5.82.85-4.21 4.1 1 5.8z" />
          </svg>
        </button>
      ))}
    </div>
  );
}
