// components/catalogo/ReviewList.tsx
"use client";

import StarRating from "./StarRating";

import { Reseña } from "@/lib/types";

type Props = { reviews: Reseña[] };

/**
 * **ReviewList**
 *
 * Lista las reseñas de un producto con su calificación en estrellas.
 * Muestra un placeholder cuando no hay reseñas.
 */
export default function ReviewList({ reviews }: Props) {
    if (reviews.length === 0) {
        return (
            <div className="text-gray-500 italic text-center py-8">
                Sin reseñas todavía
            </div>
        );
    }

    return (
        <div className="flex flex-col gap-4">
            {reviews.map((r) => (
                <div
                    key={r.id}
                    className="flex flex-col gap-1 border-b dark:border-white/30 pb-3 last:border-b-0 last:pb-0">
                    <div className="flex items-center gap-2">
                        <span className="font-bold text-green-700">
                            {r.usuario}
                        </span>
                        <StarRating value={r.calificacion} readOnly />
                        <span className="ml-auto text-xs text-gray-400">
                            {r.fecha}
                        </span>
                    </div>
                    <p className="text-gray-700 dark:text-white">
                        {r.comentario}
                    </p>
                </div>
            ))}
        </div>
    );
}
