"use client";

import Image from "next/image";
// 1. Importa el tipo unificado desde la nueva ubicación.
import { ProductoConPromo } from "@/lib/types";
import { getTotalPromo, getPromoLabel } from "@/lib/precio-producto";
import { useWishlist } from "@/context/WishListContext";

// TODO: El siguiente paso sería migrar las reseñas a un servicio API.
import { reseñas } from "../data/reviews";

import { Button } from "@/components/ui/button";
import { Heart } from "lucide-react";

interface Props {
    /** Producto con su promoción ya integrada y adaptada. */
    producto: ProductoConPromo;
    /** Añade una unidad (rápida) al carrito. */
    onAdd: (p: ProductoConPromo) => void;
    /** Abre el modal detalle. */
    onSelect: (p: ProductoConPromo) => void;
}

// --- Helpers internos ---
const obtenerPromedioCalificacion = (productoId: number): number | null => {
    const res = reseñas.filter((r) => r.productoId === productoId);
    if (!res.length) return null;
    return res.reduce((acc, r) => acc + r.calificacion, 0) / res.length;
};

const RatingBadge = ({ rating }: { rating?: number | null }) => (
    <span className="inline-flex items-center gap-1 px-3 py-1 rounded-full bg-yellow-50 text-gray-800 text-base font-bold shadow-sm">
        <svg
            viewBox="0 0 20 20"
            className="w-5 h-5 text-yellow-400"
            fill="currentColor">
            <path d="M10 15l-5.09 2.67 1-5.8-4.21-4.1 5.82-.85L10 2.5l2.48 5.02 5.82.85-4.21 4.1 1 5.8z" />
        </svg>
        {typeof rating === "number" ? (
            rating.toFixed(1)
        ) : (
            <span className="opacity-50">–</span>
        )}
    </span>
);

// --- Componente UI ---
export default function ProductCard({ producto, onAdd, onSelect }: Props) {
    // 2. Las funciones de utilidad ahora usan `producto.promo` directamente.
    const { unit: precioPromo, old: precioOld } = getTotalPromo(
        producto.promo,
        producto.price,
        1
    );
    const promoLabel = getPromoLabel(producto.promo);

    const promedio = obtenerPromedioCalificacion(producto.id);
    const hasPromo = Boolean(producto.promo);
    const srcImg = producto.image.startsWith("/")
        ? producto.image
        : `/images/${producto.image}`;

    const { addToWishlist, removeFromWishlist, isInWishlist } = useWishlist();
    const isProductInWishlist = isInWishlist(producto.id);

    const handleWishlistClick = (e: React.MouseEvent) => {
        e.stopPropagation();
        if (isProductInWishlist) {
            removeFromWishlist(producto.id);
        } else {
            addToWishlist(producto);
        }
    };

    return (
        <div
            onClick={() => onSelect(producto)}
            className={`relative cursor-pointer bg-white dark:[background-color:oklch(0.83_0.01_258.02)]
                 rounded-2xl shadow-md hover:shadow-xl transition p-4 flex flex-col items-center
                 ${
                     hasPromo
                         ? "border-4 border-blue-400"
                         : "border border-transparent"
                 }`}>
            {hasPromo && (
                <div className="absolute top-3 right-3 flex flex-col items-end z-10">
                    <span className="bg-blue-500 text-xs font-simpson text-outline-blue text-yellow-300 px-3 py-1 rounded-full shadow-md mb-1">
                        PROMO
                    </span>
                    <span
                        className="text-sm font-simpson text-outline-yellow text-blue-600 drop-shadow-sm"
                        style={{ letterSpacing: "1px" }}>
                        {promoLabel}
                    </span>
                </div>
            )}

            <Image
                src={srcImg}
                alt={producto.name}
                width={128}
                height={128}
                loading="lazy"
                className="w-32 h-32 object-contain mb-4"
            />

            <h2 className="text-xl font-semibold text-gray-800 text-center">
                {producto.name}
            </h2>

            <div className="absolute top-3 left-3 z-10">
                <RatingBadge rating={promedio} />
            </div>

            {precioOld ? (
                <div className="flex items-center gap-2 mb-2">
                    <span className="text-red-500 line-through text-lg">
                        ${precioOld.toFixed(2)}
                    </span>
                    <span className="text-green-500 text-xl font-bold">
                        ${precioPromo.toFixed(2)}
                    </span>
                </div>
            ) : (
                <p className="text-green-500 dark:text-black font-bold mb-2">
                    ${precioPromo.toFixed(2)}
                </p>
            )}

            <div className="flex items-center justify-center gap-2 mt-auto">
                <Button
                    onClick={handleWishlistClick}
                    className={`w-10 h-10 rounded-full flex items-center justify-center
                                transition-all duration-200 ease-in-out transform
                                hover:scale-110 focus:outline-none cursor-pointer dark:hover:bg-blue-950
                                ${
                                    isProductInWishlist
                                        ? "bg-pink-500 text-white shadow-lg"
                                        : "bg-yellow-400 dark:bg-gray-100 text-white dark:text-gray-500 hover:text-pink-500 shadow-xs  "
                                }`}
                    aria-label="Añadir a la lista de deseos">
                    <Heart
                        className={`w-5 h-5 transition-all duration-200 ${
                            isProductInWishlist ? "fill-current" : ""
                        }`}
                    />
                </Button>
                <Button
                    onClick={(e) => {
                        e.stopPropagation();
                        onAdd(producto);
                    }}
                    className="bg-green-500 hover:bg-green-600 text-white px-4 py-2 rounded-xl transition cursor-pointer">
                    Añadir al carrito
                </Button>
            </div>
        </div>
    );
}
